























// @ts-ignore
import {Component, Vue} from 'vue-property-decorator';
@Component({
    props: {
        messages: {
            type: Array,
            required: true,
        },
    },
    watch: {
        messages: 'onMessageChanged',
    },
})
export default class FlowerMessage extends Vue {
    public  userMessage: any[] = [];
    public mounted(): void {
        this.onMessageChanged();
    }
    public onMessageChanged(): void {
        if ((this as any).messages.length <= 2) {
            this.userMessage = (this as any).messages;
        } else {
            this.userMessage = (this as any).messages.slice((this as any).messages.length - 2);
        }

        const list: any =  this.$refs.flowerMessageList;
        const height: number = list.offsetHeight || 0; // 当前高度
        const domList: HTMLCollectionOf<Element> = document.getElementsByClassName('flowerMessage'); // 所有的元素
        if (domList.length > 0 ) {
            const lastDome: any = domList[domList.length - 1];
            this.scrollAnimation(list, lastDome.offsetTop, height);
        }
    }

    public scrollAnimation(actor: any, currenTop: number, scrollHeight: number): void {
        if (currenTop <= 0 || scrollHeight <= 0) { return; }
        actor.scrollTop = scrollHeight;
        let begin: number = 0;
        let timer: any = setInterval( () => {
            if ( begin < scrollHeight ) {
                actor.scrollTop += 5;
                begin += 5;
            } else {
               window.clearInterval(timer);
               timer = null;
            }
        }, 100);
    }
}
