














import {Component, Vue} from 'vue-property-decorator';
@Component({
    props: {
        progressType: {
            type: Number,
            required: true,
        },
        rate: {
            type: Number,
            required: true,
        },
    },
    watch: {
        rate: 'onRateChanged',
    },
})
export default class AnnularProress extends Vue {
    public showColor: string = ' red '; // 当前进度的颜色
    public progress: number[] = [0, 100]; // 当前的进度
    public girth: number = 10;
    public mounted(): void {
        this.resetRem();
        const  that: AnnularProress = this;
        window.onresize = () => { that.resetRem(); };
    }
    public resetRem(): void {
        const dom: any = document.documentElement;
        const rootSize: string = dom!.style.fontSize;
        const fontSize: number = parseFloat(rootSize.replace('px', ''));
        this.girth = fontSize * 0.15 * 2 * Math.PI;
        this.computeRate();
    }
    public onRateChanged(): void {
        this.computeRate();
    }
    public computeRate(): void {
        const start: number = (this as any).rate * 1.0 / 100 * this.girth;
        let end: number = this.girth - start;
        end = this.girth + 300;
        this.progress = [ start, end];
        if ((this as any).rate <= 70) {
            if ((this as any).progressType === 0) {
                this.showColor = '#25b077';
            } else {
                this.showColor = '#239ab4';
            }
        } else if ((this as any).rate <= 90) {
            this.showColor = '#e79827';
        } else {
            this.showColor = '#e12810';
        }
    }
}
