



















































































































































































































































































































import {Component, Vue} from 'vue-property-decorator';
import {EOS} from '@/modules/eos/eos';
import {RedBlackController} from '@/modules/contract/redblack/redBlackController';
import {RedBlackBetType, RedBlackGame} from '@/modules/contract/redblack/redBlackGameTable';
import Toast from '@/components/toast.vue';
import message from './flowerMessage.vue';
import JS2CSSKeyframes from 'js2css3';

class BetTypeOption {
    public constructor(public readonly betType: number,
                       public readonly title: string) {
    }
}

@Component({
    components: {
        Toast,
        message,
    },
})
export default class FlowerView extends Vue {
    public readonly inviteLink: string = 'https://godapp.github.io/#/flower';
    public readonly control: RedBlackController = new RedBlackController(EOS.instance);
    public userListShow: boolean = false;
    public settingShow: boolean = false;
    public inviteShow: boolean = false;
    public copySuc: boolean = false;
    public langCn: boolean = false;
    public notype: boolean = false;
    public tipsTxt: string = '';
    public type: string = '';
    public betBling: number = 1;
    public selfNum: object = { red: 0, blue: 0, luck: 0 };
    public betButtons: any[] =
            [RedBlackBetType.RedWin, RedBlackBetType.BlackWin, RedBlackBetType.Pair,  RedBlackBetType.Straight,
                RedBlackBetType.Flush, RedBlackBetType.StraightFlush, RedBlackBetType.ThreeOfAKind].map((value) =>
        new BetTypeOption(value, this.control.typeToString(value)),
    );
    public anis: any[] = [];

    public messageArray: any[] = []; // 消息数组


    public mounted(): void {
        setInterval( () => {
            this.messageArray.push({name: 'this is name', num: this.messageArray.length, isRed: this.messageArray.length % 2 === 0});
        }, 2000);


        this.control.initialize((this as any).$route.query['ref']);
    }

    public updated(): void {
        let sum1: number = 0;
        const sum2: number = 0;
        const sum3: number = 0;
        for (const v of this.control.bets) {
            if (v.betType === 1) {
                sum1 += +v.bet.toString().split(' ')[0];
            }
        }
    }

    public get cpuPer(): string {
        const obj = this.control.accountControl.accountInfo.cpuLimit;
        if (obj) {
            return (Number(obj.used) / Number(obj.max) * 100).toFixed(2);
        }
        return '0';
    }

    public betting(num: number): void {
        if (this.betBling === 0) {
            return;
        }
        if (this.notype) {
            return;
        }
        if (!this.type) {
            this.notype = true;
            setTimeout(() => {
                this.notype = false;
            }, 2000);
            return;
        }
        const type = this.type;
        const $html: any = document.documentElement;
        const $box: any = type === 'red' ?
            document.querySelector('.betting-box .red') :
            (type === 'blue' ? document.querySelector('.betting-box .blue') :
            document.querySelector('.betting-box .lucky-box'));
        const w: number = $box.offsetWidth;
        const h: number = $box.offsetHeight;
        const h1: number = $box.querySelector('.num-box').offsetHeight;
        const w2: number = $box.querySelector('.bg').offsetWidth - 10;      // 下注区文字宽高
        const h2: number = $box.querySelector('.bg').offsetHeight - 10;
        const w3: number = 21 * parseInt($html.style.fontSize, 10) / 100;   // 下注币宽高
        const h3: number = 26 * parseInt($html.style.fontSize, 10) / 100;
        const w4: number = 245 * parseInt($html.style.fontSize, 10) / 100;  // 豹子等文字宽高
        const h4: number = 32 * parseInt($html.style.fontSize, 10) / 100;
        const w5: number = 64 * parseInt($html.style.fontSize, 10) / 100;   // 幸运一击文字宽
        const l: number = +(Math.random() * (w - 10 - w3)).toFixed(2) + 5;
        const t: number = +(Math.random() * (h - 10 - h3 - h1)).toFixed(2) + 5 + h1;
        if (type === 'red' || type === 'blue') {
            if (l <= (w - w2) / 2 + w2 && l >= (w - w2) / 2 - w3) {
                if (t <= (h - h2 - h1) / 2 + h2 + h1 && t >= (h - h2 - h1) / 2 + h1 - h3) {
                    this.betting(num);
                    return;
                }
            }
        } else {
            if (l <= (w - w5) / 2 + w5 && l >= (w - w5) / 2 - w3) {
                this.betting(num);
                return;
            }
            if ((l <= (w - w5) / 2 - w3 && l >= (w - w4) / 2) || (l <= w - (w - w5) / 2 - w3 && l >= (w - w5) / 2 + w5)) {
                if (t >= (h - h4 - h1 - h3) || t < h1) {
                    this.betting(num);
                    return;
                }
            }
        }

        let bet: number = 1;
        switch (num) {
            case 0.5:
                bet = 1;
                break;
            case 1:
                bet = 2;
                break;
            case 5:
                bet = 3;
                break;
            case 10:
                bet = 4;
                break;
            case 50:
                bet = 5;
                break;
            default:
                bet = 0;
        }
        if (this.betBling !== bet) {
            this.betBling = bet;
            return;
        }

        this.selfNum[type] += num;
        this.selfNum = Object.assign({}, this.selfNum);

        const $bet = document.createElement('img');
        $bet.className = 'bet-img';
        $bet.src = require(`../../../static/flower/bet${bet}.svg`);
        $bet.style.left = l + 'px';
        $bet.style.top = t + 'px';
        const id = Math.random().toString().slice(2, 8);
        $bet.style.animation = `svgMoveX-${id} 1s linear alternate, svgMoveY-${id} 1s cubic-bezier(0.36, 0, 1, 1) alternate`;

        const kfrX: JS2CSSKeyframes = new JS2CSSKeyframes(`svgMoveX-${id}`, {
            '0%': { left: type === 'blue' ? '-0.8rem' : '1rem' },
            '100%': { left: l + 'px' },
        });
        const kfrY: JS2CSSKeyframes = new JS2CSSKeyframes(`svgMoveY-${id}`, {
            '0%': { top: type === 'luck' ? '2.1rem' : '3rem' },
            '100%': { top: t + 'px' },
        });
        this.anis.push({x: `svgMoveX-${id}`, y: `svgMoveY-${id}`});
        $box.appendChild($bet);
    }

    public select(type: string): void {
        this.type = type;
        switch (type) {
            case 'red':
                this.control.setBetType(this.betButtons[0].betType);
                break;
            case 'blue':
                this.control.setBetType(this.betButtons[1].betType);
                break;
            default:
                this.control.setBetType(4);
        }
    }

    public bet(): void {
        this.control.makeBet(this.selfNum['red'] + this.selfNum['blue'] + this.selfNum['luck']);
        if (this.control.betControl.betSuc) {
            this.type = '';
            this.betBling = 0;
            for (const v of this.anis) {
                JS2CSSKeyframes.remove(v.x);
                JS2CSSKeyframes.remove(v.y);
            }
            this.anis = [...[]];
        }
    }

    public copys(): void {
        this.copySuc = true;
        this.tipsTxt = this.langCn ? '复制成功！' : 'Copy succeed!';
        setTimeout(() => {
            this.copySuc = false;
            this.tipsTxt = '';
        }, 2000);
    }

    public beforeDestroy(): void {
        this.control.destroy();
    }
}
