
























// @ts-ignore
import {Component, Vue} from 'vue-property-decorator';
import {DiceController} from '@/modules/contract/dice/diceController';
import toast from '../../components/toast.vue';
@Component({
    components: {
        toast,
    },
    props: {
        control: {
            type: DiceController,
            required: true,
        },
    },
})
export default class DiceShakeH5 extends Vue {
    public readonly baseUrl = 'https://godapp.github.io/#/dice';
    public copySuc: boolean = false;
    public tipsTxt: string = '';
    public closeSummary(): void {
            this.$emit('closeRecommend');
    }
    // 复制成功
    public copys(): void {
        this.copySuc = true;
        this.tipsTxt = (this as any).isCN ? '复制成功！' : 'Copy succeed!';
        const  that: DiceShakeH5 = this;
        setTimeout(() => {
            that.copySuc = false;
            that.tipsTxt = '';
        }, 2000);
    }
}
