































import {Component, Vue} from 'vue-property-decorator';
import {DiceController} from '@/modules/contract/dice/diceController';
@Component({
     props: {
        control: {
            type: DiceController,
            required: true,
        },
    },
})
export default class DiceSuccess extends Vue {
    public timer: any = null;
    public musicSuccess: any = document.getElementById('diceSuccessMusic');
    public moneyMusic: any = document.getElementById('diceMoneyMusic');
    public musicFail: any = document.getElementById('diceFailMusic');
    public mounted(): void {

        this.timer = setTimeout(() => {
            this.submit();
        }, 2500);
        if ((this as any).control.isSoundOn  ) {
            if ((this as any).control.isWin()) {
                this.moneyMusic.load();
                this.moneyMusic.play();
            } else {
                this.musicFail.load();
                this.musicFail.play();
            }

        }
        const  timer: any = setTimeout(() => {
            if ( this.musicSuccess && (this as any).control.isSoundOn && this.winCount > 1 ) {
                this.musicSuccess.load();
                this.musicSuccess.play();
            }
            window.clearTimeout(timer);
        }, 500);
    }
    private get getControl(): DiceController {
        return (this as any).control;
    }

    public get winCount(): number {
        return this.getControl.winCount;
    }

    public submit(): void {
       this.dismiss();
    }

    public dismiss(): void {
        if (this.timer) {
            window.clearTimeout(this.timer);
            this.timer = null;
        }
        this.getControl.clearResult();
    }
    public beforeDestroy(): void {
        this.musicSuccess.pause();
        this.moneyMusic.pause();
        this.musicFail.pause();
        if (this.timer) {
            window.clearTimeout(this.timer);
            this.timer = null;
        }
    }
}
