




















// @ts-ignore
import {Component, Vue} from 'vue-property-decorator';
import {DiceController} from '@/modules/contract/dice/diceController';
@Component({
    props: {
        control: {
            type: DiceController,
            required: true,
        },
    },
})
export default class DiceShareH5 extends Vue {
    // 禁止操作
    public stopEvent(): void {
    //    do any thing
    }
    // 退出
    public loginOut(): void {
        (this as any).control.logout();
        this.closeShare();
    }
    public closeShare(): void {
        this.$emit('closeShare');
    }
    // 邀请好友
    public invite(): void {
        this.$emit('openReCom');
    }

    // 加入电报群
    public addTelex(): void {
        this.$emit('openBla');
    }
    // 音乐
    public bgmSet(): void {
        (this as any).control.toggleBGM();
        this.$emit('bgm');
    }
    // 音效
    public musicSet(): void {
        (this as any).control.toggleSound();
    }
}
