import {BetCallback, BetController} from '@/modules/contract/betController';
import {HouseContract} from '@/modules/contract/house/houseContract';
import {EOS} from '@/modules/eos/eos';
import {AccountInfoControl} from '@/modules/contract/accountInfoControl';
import {GameContract} from '@/modules/contract/house/gameContract';
import {TransactionResult} from '@/modules/eos/transactionResult';
import {BGM, Locale, Sound} from '@/modules/locale';
import {Localization} from '@/modules/utils/localization';

export abstract class GameController implements BetCallback {
    public readonly localization: Localization;
    public readonly house: HouseContract;
    public readonly accountControl: AccountInfoControl;
    public readonly betControl: BetController;
    public abstract contract: GameContract;

    protected constructor(public readonly eos: EOS) {
        this.house = new HouseContract(eos);
        this.localization = new Localization();
        this.accountControl = AccountInfoControl.getInstance(eos);
        this.betControl = new BetController(eos, this, this.accountControl);

        this.accountControl.ensureUpToDate();
    }

    public abstract initialize(ref: string);
    public abstract destroy(): void;

    public loggedIn(): boolean {
        return this.eos.loggedIn();
    }

    public login(): Promise<any> {
        return this.eos.connector.login();
    }

    public logout(): Promise<any> {
        return this.eos.connector.logout();
    }

    public referralLink(baseUrl: string): string {
        return baseUrl + (this.eos.loggedIn() ? ('?ref=' + this.eos.account.name) : '');
    }

    public get isCN(): boolean {
        return this.localization.locale === Locale.CN;
    }

    public setLocale(locale: Locale): void {
        this.localization.setLocal(locale);
    }

    public toggleLocale(): void {
        this.setLocale(this.isCN ? Locale.EN : Locale.CN );
    }

    public setBGM(bgm: BGM): void {
        this.localization.setBGM(bgm);
    }

    public get isBgmOn(): boolean {
        return this.localization.bgm === BGM.BGM_ON;
    }

    public toggleBGM(): void {
        this.setBGM(this.isBgmOn ? BGM.BGM_OFF : BGM.BGM_ON );
    }

    public setSound(sound: Sound): void {
        this.localization.setSound(sound);
    }

    public get isSoundOn(): boolean {
        return this.localization.sound === Sound.Sound_ON;
    }

    public toggleSound(): void {
        this.setSound(this.isSoundOn ? Sound.Sound_OFF : Sound.Sound_ON );
    }

    public abstract betText(): string;
    public abstract canMakeBet(): boolean;
    public abstract makeBet(amount: number): Promise<TransactionResult>;

}
