




















































// @ts-ignore
import {Component, Vue} from 'vue-property-decorator';
import {DiceController} from '@/modules/contract/dice/diceController';
@Component({
     props: {
     control: {
             type: DiceController,
             required: true,
         },
        loading: {
            type: Boolean,
            required: true,
        },
    },
})
export default class DiceHistoryH5 extends Vue {
    public navSelect: number = 0;
    public buttonMusic: any = document.getElementById('diceButtonMusic');
    public selectNav(nav: number): void {
        if ((this as any).control.isSoundOn) {
            this.buttonMusic.play();
        }
        this.navSelect = nav;
    }
    public mounted(): void {
        const mainDom: any = document.getElementById('dice_history_table');
        const navDom: any = document.getElementById('info-nav');
        navDom.style.width = mainDom.clientWidth - 1 + 'px';
    }

}
