import Vue from 'vue';
import {GameController} from '@/modules/contract/gameController';
import {EOS} from '@/modules/eos/eos';

export abstract class GameView<T extends GameController> extends Vue {
    public control: T = this.createController(EOS.instance);

    public abstract createController(eos: EOS): T;

    public mounted(): void {
        const network: string = this.$route.query['network'];
        if (network === 'kylin') {
            EOS.switchToTestnet();
            this.control = this.createController(EOS.instance);
        }

        this.control.initialize(this.$route.query['ref']);
    }

    public beforeDestroy(): void {
        this.control.destroy();
    }
}

