import {isEmptyString, isPresent} from '@/modules/utils/commonFunctions';

export enum TransactionError {
    None, Unknown, Locked, UserRejected, Invalid, Failure, Resource,
}

export class TransactionResult {
    public static fromReceipt(result: any): TransactionResult {
        return new TransactionResult(
                result.transaction_id,
                result.processed.block_num,
                result.processed.receipt.status === 'executed',
                TransactionError.None,
        );
    }

    public static fromError(error: any): TransactionResult {
        const errorString: string = error.type;
        let errorType: TransactionError = TransactionError.Unknown;
        if (!isEmptyString(errorString)) {
            errorType = this.stringToErrorType(errorString);
        } else if (typeof error === 'string') {
            const errorDetail: any = JSON.parse(error);
            const serverCode: number = errorDetail.code;
            if (serverCode === 500 && isPresent(errorDetail.error) && isPresent(errorDetail.error.code)) {
                errorType = this.errorCodeToErrorType(errorDetail.error.code);
            }
        }

        return new TransactionResult(
                '', -1, false, errorType,
        );
    }

    // https://github.com/EOSIO/eos/blob/master/libraries/chain/include/eosio/chain/exceptions.hpp#L100
    private static errorCodeToErrorType(errorCode: number): TransactionError {
        const code: number = Math.floor(errorCode / 10000);
        switch (code) {
            case 301:
            case 304:
                return TransactionError.Invalid;
            case 305:
                return TransactionError.Failure;
            case 308:
                return TransactionError.Resource;
            default:
                return TransactionError.Unknown;
        }
    }

    private static stringToErrorType(message: string): TransactionError {
        switch (message) {
            case 'signature_rejected':
                return TransactionError.UserRejected;
            case 'locked':
                return TransactionError.Locked;
            default:
                return TransactionError.Unknown;
        }
    }

    public constructor(public readonly id: string,
                       public readonly blockNumber: number,
                       public readonly executed: boolean,
                       public readonly errorType: TransactionError) {
    }

    public get isError(): boolean {
        return this.errorType !== TransactionError.None;
    }
}
