import {GameController} from '@/modules/contract/gameController';
import {Token} from '@/modules/eos/token';
import {Asset} from '@/modules/eos/asset';
import {RoundBasedGameController} from '@/modules/contract/roundBasedGame/roundBasedGameController';

export class ActionButton {
    public constructor(public readonly name: string,
                       public readonly action: () => Promise<string>) {
    }
}

export class GameActionButtons<T extends GameController> {
    public actions: ActionButton[] = [];
    public constructor(public controller: T) {
        this.addAction('Tokens', (c) => c.contract.tokens.readAll());
        this.addAction('Players', (c) => c.contract.players.readAll());
        this.addAction('Globals', (c) => c.contract.globals.readAll());
        this.addAction('Init', (c) => c.house.addGame(c.contract.name, c.contract.id));
        this.addAction('Deposit', (c) => c.house.transfer(new Asset(500, Token.EOS),
                'deposit,' + c.contract.name));
    }

    public addAction(name: string, action: (_: T) => Promise<any>): void {
        const controller: T = this.controller;
        this.actions.push(
                new ActionButton(
                        name, async () => {
                            const result: any = await action(controller);
                            return JSON.stringify(result);
                        },
                ),
        );
    }
}

export class RoundBasedGameButtons<T extends RoundBasedGameController<any>> extends GameActionButtons<T> {
    public constructor(controller: T) {
        super(controller);

        this.addAction('Game', (c) => c.contract.gameTable.readAll());
        this.addAction('Results', (c) => c.contract.resultTable.readAll());
        this.addAction('Close', async (c) => {
            const game: any[] = await c.contract.gameTable.readAll();
            return c.contract.reveal(game[0].id);
        });
    }
}
