import {DiceContract} from './diceContract';
import {Asset} from '../../eos/asset';
import {Token} from '../../eos/token';
import {EOS} from '../../eos/eos';
import {TransactionResult} from '../../eos/transactionResult';
import {DiceHistoryRow} from './diceHistoryTable';
import {isEmptyArray, isPresent} from '../../utils/commonFunctions';
import {GameController} from '@/modules/contract/gameController';

export class DiceController extends GameController {
    private static MY_HISTORY_SIZE: number = 50;

    public betNumber: number = 49;
    public allBetTicker: any = null;
    public allTableLoading: boolean = true;

    public rollValue: string = '00';
    public allData: DiceHistoryRow[] = [];
    public lastResult?: DiceHistoryRow;
    public winCount: number = 0;
    public rollStartTime: number = 0;
    public referer: string = '';

    public isLoading: boolean = false; // 是否需要摇塞子动画

    public showResult: boolean = false; // 是否展示本局结果
    public myData: DiceHistoryRow[] = []; // 我的历史

    public readonly contract: DiceContract;

    public constructor(eos: EOS) {
        super(eos);
        this.contract = new DiceContract(this.house);
    }

    public getName(): string {
       return this.eos.loggedIn() ? this.eos.account.name : '';
    }
    public  toLogin(): void  {
         this.eos.connect();
    }

    public curOdds(): string {
        return this.getOdds(this.betNumber).toFixed(2);
    }

    public curProfit() {
        return (this.betControl.amount * this.getOdds(this.betNumber)).toFixed(2);
    }

    public initialize(referer: string): void {
        this.referer = isPresent(referer) ? referer : '';
        this.startTicker();
    }

    public destroy(): void {
        this.stopTicker();
    }

    public getOdds(betNumber: number) {
        return (100 - 2) / betNumber;
    }

    public startTicker(): void {
        const that: DiceController = this;
        this.stopTicker();

        this.allBetTicker = setInterval(() => {
            that.updateAllBetTable();
        }, 3000);
    }

    public stopTicker(): void {
        if (this.allBetTicker !== null) {
            clearInterval(this.allBetTicker);
            this.allBetTicker = null;
        }
    }

    public getTimeStamp(): number {
        const date = new Date();
        return Math.floor(date.getTime() / 1000);
    }

    public async updateAllBetTable(): Promise<any> {
        this.allData = await this.contract.historyTable.readAll();

        if (this.eos.loggedIn()) {
            const lastId: number = isEmptyArray(this.myData) ? 0 : this.myData[0].betId;
            const myAccount: string = this.eos.account.name;
            let myBets: DiceHistoryRow[] =
                    this.allData.filter((data) => data.betId > lastId && data.player === myAccount );
            if (myBets.length > 0) {
                myBets = myBets.concat(this.myData);
                if (myBets.length > DiceController.MY_HISTORY_SIZE) {
                    myBets.splice(DiceController.MY_HISTORY_SIZE);
                }
                this.myData = myBets;
            }
        } else {
            this.myData = [];
        }

        this.allTableLoading = false;
    }

    public async updateBetResult(txid: string): Promise<any> {
        await this.eos.waitUntilImmutable(txid);
        this.getRollValueFromTrans();
    }

    public async getRollValueFromTrans(): Promise<any> {
        try {
            const result: DiceHistoryRow | undefined = this.getRollValueFromTable(this.rollStartTime);
            if (isPresent(result)) {
                const rollValue = result!.rollNumber;
                this.rollValue = rollValue < 10 ? '0' + rollValue : rollValue.toString(10);
                this.betControl.finishBet(result!.profit.quantity);
                this.updateLastResult(result!);
                return;
            }
        } catch (e) {
            console.log(e);
        }

        setTimeout(() => {
            this.getRollValueFromTrans();
        }, 1000);
    }

    public getRollValueFromTable(time: number): DiceHistoryRow | undefined {
        const accountName: string = this.eos.account.name;
        for (const bet of this.allData) {
            if (bet.player === accountName && bet.timestamp.value >= time) {
                return bet;
            }
        }
        return undefined;
    }

    public betText(): string {
        return 'Roll Under ' + this.betNumber;
    }

    public async makeBet(amount: number): Promise<TransactionResult> {
        this.clearResult();
        this.isLoading = true;

        this.rollStartTime = this.getTimeStamp();
        const result: TransactionResult = await this.contract
                .play(this.betNumber, new Asset(amount, Token.EOS), this.referer);

        if (result.isError) {
            this.isLoading = false;
        } else {
            this.updateBetResult(result.id);
        }
        return result;
    }

    public canMakeBet(): boolean {
        return true;
    }

    // 关闭本局结果
    public clearResult(): void {
        this.showResult = false;
        this.lastResult = undefined;
        this.isLoading = false;
    }

    public updateLastResult(result: DiceHistoryRow): void {
        this.showResult = true;
        this.lastResult = result;
        this.winCount = result.isWin ? (this.winCount + 1) : 0;
        this.isLoading = false;
    }

    public shouldShowResult(): boolean {
        return this.showResult && isPresent(this.lastResult);
    }

    public dismissResult(): void {
        this.showResult = false;
    }

    public isWin(): boolean {
        return isPresent(this.lastResult) && this.lastResult!.isWin;
    }

    public referralLink(baseUrl: string): string {
        return baseUrl + (this.eos.loggedIn() ? ('?ref=' + this.eos.account.name) : '');
    }
    // 菜单其他链接 如电报群 其他游戏链接
    public otherLink(): any[] {
        // 格式要求 图片地址 链接  打开方式
        // {pic: '../../static/dice-h5/telex.png', link: 'https://t.me/godappofficial', target: '_blank'}
        return [];
    }

}
