import Vue from 'vue';
import Vuex from 'vuex';
import {Locale, BGM, Sound} from '@/modules/locale';
import VuexPersistence from 'vuex-persist';

export interface State {
    locale: Locale;
    bgm: BGM;
    sound: Sound;
}

Vue.use(Vuex);

const vuexLocal = new VuexPersistence<State>({
    storage: window.localStorage,
});
export default new Vuex.Store<State>({
    state: {
        locale: Locale.EN,
        bgm: BGM.BGM_ON,
        sound: Sound.Sound_ON,
    },
    mutations: {
        setLocale(state: State, locale: Locale) {
            state.locale = locale;
        },
        setBGM(state: State, bgm: BGM) {
           state.bgm = bgm;
        },
        setSound(state: State, sound: Sound) {
            state.sound = sound;
        },
    },
    plugins: [vuexLocal.plugin],
});
