




















import {Component, Vue} from 'vue-property-decorator';
import {Scatter} from '@/modules/eos/scatter';
import {EOS} from '@/modules/eos/eos';
import {Constants} from '@/modules/eos/constants';
import {RedBlackController} from '@/modules/contract/redblack/redBlackController';
import {ActionButton, GameActionButtons, RoundBasedGameButtons} from '@/modules/tools/actionButton';
import {DiceController} from '@/modules/contract/dice/diceController';
import {BlackjackController} from '@/modules/contract/blackjack/blackjackController';
import {BaccaratController} from '@/modules/contract/baccarat/baccaratController';
import {Network} from '@/modules/eos/network';

@Component
export default class Manage extends Vue {
    public message: string = '';
    public eos = new EOS(new Scatter(Network.MAINNET));
    public games: Array<GameActionButtons<any>> = [];

    public current: GameActionButtons<any> = new GameActionButtons(new DiceController(this.eos));

    public mounted(): void {
        const network: string = this.$route.query['network'];
        if (network === 'kylin') {
            EOS.switchToTestnet();
            this.eos = EOS.instance;
        }

        this.games = [
                new GameActionButtons(new DiceController(this.eos)),
                new GameActionButtons(new BlackjackController(this.eos)),
                new RoundBasedGameButtons(new BaccaratController(this.eos)),
                new RoundBasedGameButtons(new RedBlackController(this.eos)),
        ];
        this.current = this.games[0];
    }

    public async connect(): Promise<any> {
        if (this.eos.loggedIn()) {
            await this.eos.connector.logout();
        } else {
            await this.eos.connector.login();
            this.message = 'Hello ' + this.eos.connector.getAccount();
        }
    }

    public async performAction(action: ActionButton): Promise<any> {
        this.message = await action.action();
    }
}


