








































































































































































import vueSlider from 'vue-slider-component';
import {Component, Vue} from 'vue-property-decorator';
import {DiceController} from '@/modules/contract/dice/diceController';
import {BetController} from '@/modules/contract/betController';
import {EOS} from '@/modules/eos/eos';
import annularProgress from '@/components/annularProgress.vue';
import diceShare from '@/views/dice-h5/diceShareH5.vue';
import diceSummary from '@/views/dice-h5/diceSummaryH5.vue';
import diceHistory from '@/views/dice-h5/diceHistoryH5.vue';
import diceSuccess from '@/views/dice-h5/diceSuccessH5.vue';
import diceShake from '@/views/dice-h5/diceShakeH5.vue';
import diceRecommend from '@/views/dice-h5/diceRecommendH5.vue';
import diceBlame from '@/views/dice-h5/diceBlameH5.vue';
import {Locale} from '@/modules/locale';
import {GameView} from '@/views/GameView';
@Component({
    components: {
        // diceFail,
        vueSlider,
        annularProgress,
        diceShare,
        diceSummary,
        diceHistory,
        diceSuccess,
        diceShake,
        diceRecommend,
        diceBlame,
    },
})
export default class DiceView extends GameView<DiceController> {
    public openLang: boolean = false; // 开启语言选择
    public isFocus: boolean = false; // 失去焦点
    public progressWidth: number = 0; // 滚动条宽度
    public openShare: boolean = false; // 是否开启分享
    public openSummary: boolean = false; // 是否开启游戏说明
    public openHistory: boolean = false; // 是否开启历史记录
    public openRecommend: boolean = false; // 是否推荐
    public openBlame: boolean = false; // 是否免责声明
    public underSlide: any = {
        roll: this.control.betNumber,
        height: 14,
        dotWidth: 28,
        dotHeight: 28,
        min: 1,
        max: 97,
        speed: 0.1,
        tooltip: 'always',
        bgStyle: {
            backgroundColor: 'transparent',
            border: 'none',
        },
        tooltipStyle: {
            backgroundColor: 'transparent',
            border: 'none',
            overflow: 'hidden',
            background: 'url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAKUAAABmCAMAAACdiaHyAAAAYFBMVEXqo/' +
                    '8AAAD3vJzvrtbtqej8yWz6w4Lxscv9yWvyscj8yG34v5X6xH/7xH30t7X2u6P6woX0t7P6xIH2uqf5wI35wI35wI/' +
                    'ys8X6w4LspvX6xID5wYz7xnj8xnb7xH78yG0aqtI/AAAAIHRSTlMSACYXFFA2GVIVTSg8PhcbLB84IzEdBAoUBjIiQk' +
                    'YPWpHWsdsAAAJjSURBVHja7dtb06IgGMDxBzWw0EQ5SB6//7dcq21wO+zbJrPzMPG/8qKL36BQkwBklRXlePix3afl7' +
                    '7Y7DJ1dw5yy7fJ86Hjyn+OXLhcuQcd5pu2D0nR5KQFTcpyp+VM5HQYG2KrH3K6VeicAY2rWTqkPuG62K5n1TTmhRS7M' +
                    '3F6VZuCAN3UwFyVvAHODOCsNwtm9ju3aRdkrwF0hFmWBeygB6pHAhPupPDdOoHGu5+toDz3etfKWEMBrwJ6kwLFPHoA' +
                    '6FGUSgLKDHr+SLcoUsMdEIEodhLKKSk9KHpXelAkco9KTsodjBthLo9KfUgei3AegrKIyKvEViPIYld+lzKIyKhEWlV' +
                    'GJsWwflVGJr6iMSowFsqpHZVQiLCq/TZkeg/gvOAxlFcL7nmCUAbyHjMovVAawbyMQpQYdwK4nDRX+PYOsAot//2Vto' +
                    'cW8Wf0ab4EI7NMn5QSIxn7LZbUojcL92y0TZlGSHveTmejraY8O82LEhPl9cobiXdlTNd1OIVVomamyqxNdDc6bzjpL' +
                    'nJLYQuCb6VnSTXcnDUXJcS3vaUJ783BqcxIl5ZK9Kn0s+7eRSd+P1YmifH1q02Us75ryacXzuiR9b1ykaormGr10vnh' +
                    'dJ3priAvIx53afa0KKuGnlk8puW9PhnzQBqWDQk2LJPv7iRIqs/ZNoWelgy7OMnk9jg2t2xPZ0FalczZF/dxIm7olG/' +
                    'KhdE5ZUgb3MVXKDUZ/Sufkg0rvvtwGvuFWe1U6Z6YGxVbjOIht4+hX6Zx7NVKZAUAmqQ+jX6VztrwYy6IcG22Iz4B47' +
                    'mTtZIjnfgH3XC4KLytM4wAAAABJRU5ErkJggg==) 0 0/100% 100% no-repeat',
            width: '0.56rem',
            height: '0.36rem',
            textAlign: 'center',
            lineHeight: '0.36rem',
            fontSize: '0.18rem',
            padding: '0px',
        },
        processStyle: {
             backgroundColor: 'transparent',
        },
        sliderStyle: {
            backgroundColor: 'transparent',
        },
    };
    public createController(eos: EOS): DiceController {
        return new DiceController(eos);
    }

    public mounted(): void {
        super.mounted();

        this.resetRem();
        const  that: DiceView = this;
        window.onresize = () => { that.resetRem(); };
        const $bgm: any = document.getElementById('diceMusic');
        const shakeMusic: any = document.getElementById('diceShakeMusic');
        const musicSuccess: any = document.getElementById('diceSuccessMusic');
        const moneyMusic: any = document.getElementById('diceMoneyMusic');
        const musicFail: any = document.getElementById('diceFailMusic');
        $bgm.autoplay = this.control.isBgmOn;
        window.addEventListener('touchstart', this.forceSafariPlayAudio, false);
        window.addEventListener('click', this.forceSafariPlayAudio, false);
        $bgm.addEventListener('play', () => {
            window.removeEventListener('touchstart', this.forceSafariPlayAudio, false);
            window.removeEventListener('click', this.forceSafariPlayAudio, false); // 针对safari浏览器
            // 在iOS上 不能出现延迟操作 所以 先播放 在暂停
            this.initSound(shakeMusic);
            this.initSound(musicSuccess);
            this.initSound(moneyMusic);
            this.initSound(musicFail);

        }, false);
    }
    public resetRem(): void {
        const twh: number = 100 / 375;
        const mainDom: any = document.getElementById('main');
        const mainWidth: number = mainDom.innerWidth || mainDom.clientWidth;
        const trem: number = mainWidth * twh; // 当前窗口的宽度
        const domRoot: any = document.documentElement;
        domRoot.style.fontSize = trem + 'px';
        this.progressWidth = document.getElementsByClassName('rollSlide')[0].clientWidth + 20;
    }
    public forceSafariPlayAudio(): void {

        const $bgm: any = document.getElementById('diceMusic');
        $bgm.load();
        if (this.control.isBgmOn) {
            $bgm.play();
        } else {
            $bgm.pause();
        }
    }
    // 配置音效
    public initSound(sound: any) {
        sound.load();
        sound.play();
        sound.pause();
    }

    // 关闭所有弹框
    public closeSheet(): void {
        this.openLang = false;
        this.openShare = false;
        this.openSummary = false;
        this.openHistory = false;
        this.openRecommend = false;
        this.openBlame = false;
    }
    // 获取焦点
    public getFocus(): void {
        this.closeSheet();
        this.isFocus = true;
    }
    // 失去焦点事件
    public loseFocus(): void {
        this.isFocus = false;
    }
    // 关闭分享界面
    public closeShare(): void {
        this.playButtonMusic();
        this.openShare = false;
    }
    // 开启分享界面
    public toOpenShare(): void {
        this.closeSheet();
        this.playButtonMusic();
        this.openShare = true;
    }
    // 关闭游戏说明
    public closeSummary(): void {
        this.playButtonMusic();
        this.openSummary = false;
    }
    // 开启游戏说明
    public toOpenSummary(): void {
        this.playButtonMusic();
        this.openSummary = true;
    }
    // 开启历史记录
    public showHistory(): void {
        this.openLang = false;
        this.openShare = false;
        this.openSummary = false;
        this.openRecommend = false;
        this.openBlame = false;
        this.playButtonMusic();
        this.openHistory = !this.openHistory;
    }
    // 关闭推荐规则
    public closeRecommend(): void {
        this.playButtonMusic();
        this.openRecommend = false;
    }
    // 开启推荐规则
    public openReCom(): void {
        this.playButtonMusic();
        this.closeSheet();
        this.openShare = false;
        this.openRecommend = true;
    }
    // 关闭免责声明
    public closeBlame(): void {
        this.playButtonMusic();
        this.openBlame = false;
    }
    // 提交
    public submit(): void {
        this.closeSheet();
        this.playButtonMusic();
        this.control.betControl.makeBet();
    }
    // 播放按钮音效
    public playButtonMusic(): void {
        const buttonMusic: any = document.getElementById('diceButtonMusic');
        if (this.control.isSoundOn) {
            buttonMusic.load();
            buttonMusic.play();
        }

    }
    // 限制输入最大值
    public changeValue(e): boolean {
        if (e.target.value > BetController.MAX_BET) {
            e.stopPropagation();
            e.returnValue = false;
            return false;
        }
        if (this.control.betControl.amount >= BetController.MAX_BET) {
            this.control.betControl.amount = BetController.MAX_BET;
        }
        return true;
    }
    public limitValue(e): void {
        e.target.value = e.target.value.replace(/^d*(?:.d{0,2})?$/g, '');
        if (e.target.value > BetController.MAX_BET) {
            e.target.value = BetController.MAX_BET;
            this.control.betControl.amount = BetController.MAX_BET;
        }
    }
    // 点击最小
    public clickMinButton(): void {
        this.closeSheet();
        this.playButtonMusic();
        this.control.betControl.setMin();
    }
    // 点击1/2
    public clickHalfButton(): void {
        this.closeSheet();
        this.playButtonMusic();
        this.control.betControl.stepAmount( 1 / 2 );
    }
    // 点击双倍
    public clickTimesButton(): void {
        this.closeSheet();
        this.playButtonMusic();
        this.control.betControl.stepAmount( 2 );
    }
    // 点击max
    public clickMaxButton(): void {
        this.closeSheet();
        this.playButtonMusic();
        this.control.betControl.setMax();
    }
    // 按键按下 是否移动端也能这样 需要测试 否则采用 输入框变化来进行控制
    public keyPass(): void {
        const buttonMusic: any = document.getElementById('diceKeyMusic');
        if (this.control.isSoundOn) {
            buttonMusic.load();
            buttonMusic.play();
        }

    }
    public openBla(): void {
        this.closeSheet();
        this.playButtonMusic();
        this.openShare = false;
        this.openBlame = true;
    }
    public selectLanguage(value: number): void {
        this.closeSheet();
        this.playButtonMusic();
        this.control.setLocale(value === 0 ? Locale.CN : Locale.EN);
        this.openLang = false;
    }
    public openLanguage(): void {
        this.openShare = false;
        this.openSummary = false;
        this.openRecommend = false;
        this.openBlame = false;
        this.playButtonMusic();
        this.openLang = !this.openLang;
    }
    public get cpuPer(): number {
        const obj = this.control.accountControl.accountInfo.cpuLimit;
        if (obj) {
            return parseFloat((Number(obj.used) / Number(obj.max) * 100).toFixed(1));
        }
        return 0;
    }
    public get netPer(): number {
        const obj = this.control.accountControl.accountInfo.netLimit;
        if (obj) {
            return parseFloat((Number(obj.used) / Number(obj.max) * 100).toFixed(1));
        }
        return 0;
    }
}
