import store from '@/store';
import {Locale, BGM, Sound} from '@/modules/locale';

export class Localization {
    public locale: Locale;
    public bgm: BGM;
    public sound: Sound;

    public constructor() {
        this.locale = store.state.locale;
        this.bgm = store.state.bgm;
        this.sound = store.state.sound;
    }

    public setLocal(locale: Locale): void {
        store.commit('setLocale', locale);
        this.locale = locale;
    }

    public setBGM(bgm: BGM): void {
        store.commit('setBGM', bgm);
        this.bgm = bgm;
    }

    public setSound(sound: Sound): void {
        store.commit('setSound', sound);
        this.sound = sound;
    }
}
